/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params){
  return new Promise((resolve, reject) =>{
    ajax.get(url, {
      params: params
    }).then(res => {
      resolve(res);
    }).catch(err =>{
      reject(err.data);
    })
  });
}

/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    ajax.post(url, params)
      .then(res => {
        resolve(res.data);
      })
      .catch(err =>{
        reject(err.data)
      })
  });
}



